import { Datamap } from "../Datamap";

const ITEM_POWERS = {
    effective_devotee_mult, devotee_power_mult, prayer_upgrades, 
    piety_eq_mult_gain, devotion_eq_mult_gain, usd_eq_mult_gain
}

export default ITEM_POWERS;

function effective_devotee_mult(data: Datamap) {
    if (data.adornments.equiped.book) {
        if (data.adornments.equiped.book.origin === 'ruins') {
            return data.adornments.equiped.book.level + 1;
        }
    }
    return 1;
}

function devotee_power_mult(data: Datamap) {
    if (data.adornments.equiped.ring) {
        if (data.adornments.equiped.ring.origin === 'ruins') {
            return data.adornments.equiped.ring.level + 1;
        }
    }
    return 1;
}

function prayer_upgrades(data: Datamap, prodctionCallback: (deltaMS: number)=>void) {
    let eq = data.adornments.equiped;
    if (eq.cane) {
        if (eq.cane.origin == 'ruins') {
            data.prophetData.believers += eq.cane.level
        }
        if (eq.cane.origin == 'mart') {
            data.prophetData.congregation.USD += eq.cane.level
        }
        if (eq.cane.origin == 'shop') {
            prodctionCallback(eq.cane.level * 1000)
        }
    }
}

function piety_eq_mult_gain (data: Datamap) {
    let mult = 0;
    let eq = data.adornments.equiped;
    if (eq.cap) {
        if (eq.cap.origin === 'ruins') {
            mult += eq.cap.level;
        }
    } 
    //no book
    if (eq.robe) {
        if (eq.robe.origin === 'ruins') {
            mult += eq.robe.level;
        }
    } 
    //no ring
    return mult;
}

function devotion_eq_mult_gain (data: Datamap) {
    let mult = 0;
    let eq = data.adornments.equiped;
    if (eq.cap) {
        if (eq.cap.origin === 'mart') {
            mult += eq.cap.level;
        }
    } 
    if (eq.book) {
        if (eq.book.origin === 'mart') {
            mult += eq.book.level;
        }
    }
    if (eq.robe) {
        if (eq.robe.origin === 'mart') {
            mult += eq.robe.level;
        }
    } 
    if (eq.ring) {
        if (eq.ring.origin === 'mart') {
            mult += eq.ring.level;
        }
    } 
    return mult;
}

function usd_eq_mult_gain (data: Datamap) {
    let mult = 0;
    let eq = data.adornments.equiped;
    if (eq.cap) {
        if (eq.cap.origin === 'shop') {
            mult += eq.cap.level;
        }
    } 
    if (eq.book) {
        if (eq.book.origin === 'shop') {
            mult += eq.book.level;
        }
    }
    if (eq.robe) {
        if (eq.robe.origin === 'shop') {
            mult += eq.robe.level;
        }
    } 
    if (eq.ring) {
        if (eq.ring.origin === 'shop') {
            mult += eq.ring.level;
        }
    } 
    return mult;
}